import AdminDashboard from '../components/admin/Dashboard.vue'
import ProductTypevw from '../components/admin/ProductTypevw.vue'
import ProductTypeadd from '../components/admin/ProductTypeadd.vue'
const AdminRoutes = [
  {
    path: '/admin',
    name: 'Dashboard',
    component: AdminDashboard
  },
  {
    path: '/admin/productType',
    name: 'ProductTypevw',
    component: ProductTypevw
  },
  {
    path: '/admin/productType/add',
    name: 'ProductTypeadd',
    component: ProductTypeadd
  }
]

export default AdminRoutes



